/* services */
.service-frontend-wrap {
  margin: 0 4rem;
  padding: 12rem 6.4rem;
  color: $font-white;
  background-color: $bg-black;
  border-top-left-radius: $radius-default;
  border-top-right-radius: $radius-default;

  .header-copy {
    font-weight: 700;
    font-size: 6rem;
    line-height: 7rem;

    strong {
      color: $color-accent;
    }
  }

  .sub-copy {
    padding: 4rem 0 6rem;
    font-weight: 500;
    font-size: 4rem;
    line-height: 4.4rem;
  }

  .btn-more {
    display: flex;
    flex-direction: row-reverse;
    padding: 0 0 5rem;
    > a {
      position: relative;
      display: inline-flex;
      align-items: center;
      height: 4rem;
      padding: 0.5rem 4rem 0.5rem 1.6rem;
      text-transform: uppercase;
      background: transparent;
      border: 0.1rem solid #fff;
      border-radius: 2rem;
      will-change: background color;
      transition: background 0.3s ease-in-out;

      &:after {
        content: ">>";
        position: absolute;
        top: 7px;
        right: 13px;
      }

      &:hover {
        color: $font-black;
        background: $bg-white;
      }
    }
  }

  .service-fb-field {
    border-top: 1px solid #fff;

    > li {
      padding: 4rem 0;
      border-bottom: 1px solid #fff;

      > dl {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-weight: 300;
        font-size: 6rem;

        > dt {
          text-transform: uppercase;
        }

        > dd {
          font-size: 4rem;
        }
      }
    }
  }

  & + .service-ai-wrap:before {
    content: "";
    display: block;
    margin: 0 4rem;
    height: 30rem;
    background: linear-gradient($bg-black, $bg-white);
  }
}

.service-ai-wrap {
  padding: 0 0 10rem;

  .service-tit {
    position: relative;
    margin: 40rem auto 0;
    font-size: 20rem;
    letter-spacing: -1rem;
    font-weight: 700;
    text-align: center;
    line-height: 15rem;

    &:before {
      content: "*";
      position: absolute;
      top: -16rem;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .service-sub-desc {
    padding: 3rem 0 0 0;
    margin: 0 auto;
    font-size: 1.8rem;
    letter-spacing: -0.1rem;
    font-weight: 700;
    text-align: center;
  }

  .service-ai-box {
    display: flex;
    gap: 2rem;
    height: 83rem;
    margin: 10rem 4rem;

    > div {
      &:nth-child(1) {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        font-size: 4.8rem;
        font-weight: 700;
        text-align: center;
        letter-spacing: -0.2rem;
        line-height: 5rem;
        background-image: url("../../assets/images/ai_sample/gen_ai_10.jpg");
        background-position: center center;
        background-size: cover;
        background-color: $bg-white;
        border-radius: $radius-default;
      }

      &:nth-child(2) {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        font-size: 3.6rem;
        font-weight: 700;
        background: #f6f6f6;
        border: 1px solid #cdcdcd;
        border-radius: $radius-default;
      }
    }
  }

  .service-ai-gallery {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
    padding: 0 4rem;

    > div {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 42rem;
      background-color: #d9d9d9;
      border-radius: $radius-default;
      overflow: hidden;

      > img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      &.textbox {
        background-color: $bg-white;
        > p {
          display: flex;
          align-items: flex-end;
          width: 100%;
          height: 100%;
          padding: 1rem;
          justify-content: flex-start;
          color: $font-basic;
          word-break: keep-all;
        }
      }
    }
  }

  .move-thum-wrap {
    position: relative;
    height: 165rem;
    padding: 10rem 4rem;
    .move-thum-txt {
      padding: 70rem 0 0 18rem;
      font-size: 4.8rem;
      font-weight: 400;
      letter-spacing: -0.2rem;
      line-height: 6rem;
    }

    .move-thum-01,
    .move-thum-02,
    .move-thum-03,
    .move-thum-04 {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 37rem;
      height: 67rem;
      background-color: #d9d9d9;
      border-radius: $radius-default;
      overflow: hidden;

      > img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .move-thum-01 {
      top: 1007px;
      left: 194px;
    }

    .move-thum-02 {
      top: 1104px;
      left: 580px;
    }

    .move-thum-03 {
      top: 972px;
      left: 965px;
    }

    .move-thum-04 {
      top: 886px;
      left: 1351px;
    }
  }

  .middle-sub-desc {
    width: 60rem;
    height: 35rem;
    margin: 0 0 0 19.5rem;
    font-size: 3rem;
    line-height: 1.2;
    letter-spacing: -0.05rem;
    word-break: keep-all;
  }

  .swiper-item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 22rem;
    min-height: 30rem;
    max-width: 80rem;
    background-color: #d9d9d9;
    border-radius: $radius-default;
    overflow: hidden;

    > img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
