/* contact */
.app-contact {
  padding: 0 4rem 10rem;
  background-color: #f0f0f0;
  background-image: url("../../assets/images/bg_contact_line.png");
  background-repeat: no-repeat;
  background-position: center 110rem;

  .header-copy {
    padding: 10rem 0 0 0;
    display: flex;
    flex-direction: column;

    .sub-text {
      padding: 0 27rem 0 0;
      font-size: 3.2rem;
      font-weight: 500;
      text-align: right;
    }

    .main-text {
      font-size: 14rem;
      line-height: 12rem;
      letter-spacing: -1rem;
      text-align: center;
      font-weight: 500;
      padding: 0 0 3rem;
      text-transform: uppercase;
      flex-direction: column;
    }
  }

  .contact-imgbox {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;

    > div {
      > figure {
        > img {
          width: 100%;
          max-height: 60rem;
          object-fit: cover;
          border-radius: $radius-default;
        }
        > figcaption {
          padding: 0.8rem 0;
          font-size: 1.6rem;
        }
      }
    }
  }

  .contact-guide {
    padding: 11.5rem 4rem;
    text-align: center;
    line-height: 5.6rem;
    font-size: 4.8rem;
    text-transform: uppercase;
  }

  .contact-bottom-desc {
    padding: 19rem 0 15rem 0;
    font-size: 2.4rem;
    line-height: 3rem;
    text-align: right;
  }

  .contact-infobox {
    max-width: 64rem;
    margin: 0 auto;

    > dl {
      display: flex;
      align-items: center;
      padding: 0 0 1.4rem;

      > dt {
        display: flex;
        align-items: center;
        justify-content: center;
        min-width: 4.8rem;
        min-height: 4.8rem;
        font-size: 4.8rem;
        border: 2px solid $bg-black;
        border-radius: 50%;
        transform: translateY(5px);
      }

      > dd {
        padding: 0 0 0 1.8rem;
        font-size: 4.8rem;
      }

      a {
        letter-spacing: -0.1rem;
        text-decoration: underline;
        text-underline-offset: 1rem;
      }

      &:nth-child(3) {
        > dt {
          padding-bottom: 0.8rem;
        }
      }
    }

    .contact-logo {
      padding: 0 0 4rem;
    }
  }

  .contact-bottom-define {
    display: flex;
    height: 4rem;
    margin: 0 -4rem;
    font-size: 3.6rem;
    line-height: 4rem;
    white-space: nowrap;
    max-width: calc(100% + 8rem);
    overflow: hidden;
  }
}
