/* works */
.app-works {
  margin: 0;
  padding: 2rem 0;
  color: $font-black;
  background-color: $bg-white;
  overflow-x: hidden;

  .header-copy {
    display: flex;
    justify-content: center;
    padding: 0 0 4.5rem;
    border-bottom: 0.1rem solid $bg-black;

    .main-text {
      font-size: 5.2rem;
      line-height: 4.2rem;
      letter-spacing: -0.1rem;
      text-align: center;
      font-weight: 700;
      padding: 0;
      text-transform: uppercase;
      flex-direction: column;
    }
  }

  .project-title {
    padding: 0 0 4rem;
    font-size: 6rem;
    font-weight: 500;
    letter-spacing: -0.2rem;
  }

  .work-list-wrap {
    position: relative;
    // height: 200vh;
    margin: 10rem 0 0 0;
    // border: 1px solid blue;

    .work-gallery-list {
      display: grid;
      grid-template-columns: 18.5rem 15rem 16rem 13.4rem 13.8rem 15rem 17rem;
      align-items: start;
      justify-content: center;
      width: 80vw;
      margin: 0 auto;
      column-gap: 2.4rem;

      .work-item {
        cursor: pointer;

        &:nth-child(1) {
          > div {
            height: 34rem;
          }
        }
        &:nth-child(2) {
          margin-top: 13rem;
          > div {
            height: 21rem;
          }
        }
        &:nth-child(3) > div {
          height: 28rem;
        }
        &:nth-child(4) {
          margin-top: 5rem;
          > div {
            height: 16rem;
          }
        }
        &:nth-child(5) {
          margin-top: 5rem;
          > div {
            height: 16rem;
          }
        }
        &:nth-child(6) {
          margin-top: 5rem;
          > div {
            height: 27rem;
          }
        }

        &:nth-child(7) > div {
          height: 34rem;
        }

        figcaption {
          display: flex;
          align-items: center;
          height: 3rem;
          font-size: 1.4rem;
          font-weight: 500;
          text-align: left;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }

        div {
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          background-color: $bg-gray;
          border-radius: 5px;
          overflow: hidden;
          &:after {
            display: none;
          }

          &.active {
            &:after {
              content: "selected";
              position: absolute;
              top: 0;
              right: 0;
              bottom: 0;
              left: 0;
              display: flex;
              align-items: center;
              justify-content: center;
              color: $font-white;
              font-size: 1.6rem;
              width: 100%;
              height: 100%;
              background-color: rgba(0, 213, 141, 0.6);
              z-index: 1;
            }
          }
          > img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            transform: scale(1);
            will-change: transform;
            transition: transform 0.3s ease-in-out;
          }
        }

        &:hover {
          div {
            &:after {
              content: "";
              position: absolute;
              top: 0;
              right: 0;
              bottom: 0;
              left: 0;
              display: block;
              width: 100%;
              height: 100%;
              background-color: rgba(0, 213, 141, 0.6);
              z-index: 1;
            }
          }
          img {
            transform: scale(1.5);
          }
        }
      }
    }

    .project-infobox {
      position: relative;
      display: flex;
      width: 70vw;
      min-height: 30rem;
      margin: 8rem auto 0;
      padding: 2rem 5.8rem 5rem;
      background-color: $bg-color;
      flex-direction: column;
      border-top: 0.1rem solid $bg-black;
      border-bottom: 0.1rem solid $bg-black;

      .title {
        padding: 3.6rem 0 4rem;
        font-size: 3.6rem;
        font-weight: 700;
        word-spacing: 0.2rem;
        letter-spacing: -0.05rem;
        text-transform: uppercase;
      }

      .desc {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        gap: 4rem 2rem;

        .desc-unit {
          color: #171717;
          > dt {
            padding-bottom: 0.6rem;
            color: inherit;
            font-weight: 400;
            text-transform: uppercase;
          }

          > dd {
            color: inherit;
            font-weight: 700;
            line-height: 1.3;
            // text-transform: uppercase;
            letter-spacing: 0;
            word-spacing: 0.15rem;
          }

          &:nth-child(1) {
            grid-column: 1 / 4;
            grid-row: 1 / 4;
          }
          &:nth-child(2) {
            grid-column: 5 / 7;
          }
          &:nth-child(3) {
            grid-column: 7 / 9;
          }
          &:nth-child(4) {
            grid-column: 9 / 12;
          }
          &:nth-child(5) {
            grid-column: 5 / 7;
            grid-row: 2 / 4;
          }
        }
      }
    }
  }

  .mid-copy {
    width: 70vw;
    margin: 0 auto;
    .first-copy {
      padding: 13.6rem 5rem 0 0;
      span {
        display: block;
      }
    }
    .second-copy {
      padding: 15rem 5rem;
      span {
        display: block;
      }
    }
    .third-copy {
      padding: 15rem 5rem 13rem;
      span {
        display: block;
      }
    }
    .head {
      text-align: center;
      font-weight: 700;
      padding: 0 0 1.6rem 0;
      font-size: 10rem;
      color: #1d1d1d;
      line-height: 1.1;
    }
    .sub {
      text-align: center;
      font-weight: 600;
      font-size: 1.8rem;
      color: #1d1d1d;
      line-height: 1.2;
      text-transform: uppercase;
      word-spacing: 0.2rem;
      letter-spacing: 0.05rem;
    }
  }

  .project-midbox {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 70vw;
    margin: 0 auto;
    height: 30rem;
    border-radius: 0.5rem;
    overflow: visible;
    // overflow: hidden;

    .project-midbox-img {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      max-width: 100vw !important;
      object-fit: cover;
      background-color: $bg-gray;
      background-image: url("../../assets/images/midwork.jpg");
      background-size: cover;
      background-position: center;
      border-radius: 0.5rem;
      z-index: 1;
    }
    > p {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      font-weight: 400;
      letter-spacing: -0.15rem;
      font-size: 5rem;
      color: $font-white;
      z-index: 1;
    }
  }

  .section-line {
    width: 22rem;
    height: 0.1rem;
    margin: 0 auto;
    background-color: #1d1d1d;
  }

  .other-works-section {
    padding: 12rem 0;
    .head {
      font-size: 4.8rem;
      font-weight: 400;
      color: #1d1d1d;
      text-align: center;
    }
    .sub {
      padding: 1rem 0;
      text-align: center;
      font-weight: 600;
      font-size: 1.8rem;
      color: #1d1d1d;
      line-height: 1.2;
      text-transform: uppercase;
    }
    .workbox-wrap {
      display: grid;
      grid-template-columns: 1fr 2fr;
      width: 70vw;
      height: 70rem;
      margin: 5rem auto;
      & > div {
        display: flex;
        width: 100%;
        height: 100%;
        background-color: $bg-white;
        border: 0.1rem solid #1d1d1d;
        border-radius: 15rem;
      }
      .year-box {
        padding: 15rem 0;
        flex-flow: column;
        justify-content: flex-start;
        align-items: center;

        .year-ui {
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          align-items: center;
          overflow-y: hidden;
          width: 100%;
          height: 40rem;
          border: 1px solid red;
        }
      }
      .desc-box {
        display: flex;
        flex-direction: column;
        padding: 15rem 10rem;
        .desc-box-inner {
          max-height: 50rem;
          overflow-y: auto;
        }
        dl {
          display: grid;
          grid-template-columns: 12rem 1fr;
          column-gap: 2rem;
          padding: 0 0 4.6rem 0;
          color: #1d1d1d;
          font-size: 2rem;
          line-height: 1.8;
          font-weight: 400;
          dt {
            grid-column: 1/3;
            display: flex;
            flex-wrap: wrap;
            align-items: flex-end;
            padding-bottom: 1rem;
            font-size: 1.6rem;
            color: #b99128;
            font-weight: 400;

            > strong {
              padding-right: 1.5rem;
              font-size: 2.4rem;
              font-weight: 700;
              color: #1d1d1d;
            }
            > span {
              padding-left: 2rem;
              font-weight: 300;
              color: #979693;
            }
          }
          dd {
            font-size: 1.8rem;
            &.role {
              em {
                display: block;
                font-weight: 500;
                font-style: normal;
                color: #383838;
                line-height: 1.4;
                word-break: keep-all;
              }
            }
            &.info {
              display: flex;
              flex-direction: column;
              row-gap: 0.5rem;
              color: #787774;
              line-height: 1.4;
              letter-spacing: -0.02rem;
              word-spacing: 0.25rem;
              p {
                position: relative;
                word-break: keep-all;
                padding: 0 0 0 1.4rem;
                &:before {
                  content: "";
                  position: absolute;
                  top: 0.8rem;
                  left: 0;
                  display: block;
                  width: 0.8rem;
                  height: 0.8rem;
                  background-color: #a7a7a7;
                  border-radius: 50%;
                  opacity: 0.6;
                }
              }
            }
          }
        }
      }
    }
  }

  .key-project {
    display: flex;
    flex-direction: column;
    padding: 0 2rem;
    background-color: $bg-black;

    > li {
      border-bottom: 1px solid rgba(255, 255, 255, 1);
    }

    .project-item {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 1rem 0;
      font-size: 1.6rem;
      font-weight: 500;
      color: $font-white;

      .project-header {
        display: flex;
        align-items: stretch;

        i {
          position: absolute;
          top: 1rem;
          left: -3rem;
          display: block;
          font-style: normal;
          color: $font-white;
          user-select: none;
          cursor: default;
        }

        button[type="button"] {
          position: relative;
          display: flex;
          width: 20%;
          margin: 0 0 0 20%;
          justify-content: center;
          pointer-events: initial;
          cursor: pointer;
          border: none;
          outline: none;
          will-change: width;
          transition: width 1s ease-in-out;

          &.active {
            width: 40%;
          }
        }

        figure {
          width: 100%;
          gap: 1rem;

          img {
            width: 100%;
            aspect-ratio: 1 / 1;
            will-change: transform;
            object-fit: cover;
            filter: grayscale(1);
            transition: filter 0.5s ease-in-out;
          }

          &:hover {
            img {
              filter: grayscale(0);
            }
          }
        }

        figcaption {
          /* display: flex; */
          display: none;
          flex-direction: column;
          align-items: stretch;
          justify-content: flex-end;

          * {
            font-style: normal;
            font-weight: 500;
          }
        }
      }
    }

    .project-desc {
      display: none;
      font-size: 2rem;

      &.open {
        display: block;
      }
    }

    .info-unit {
      > dt {
        text-transform: uppercase;
      }
    }
  }
}
