/* feeds */
.app-feeds {
  padding: 7rem 4rem;

  .header-copy {
    display: flex;
    font-size: 8rem;
    line-height: 8rem;
    letter-spacing: -0.4rem;
    text-align: center;
    font-weight: 500;
    padding: 0 0 3rem;
    text-transform: uppercase;
  }

  .feed-list {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);

    .feed-card {
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 60rem;
      background-color: $bg-white;
      border-radius: $radius-default;
    }
  }
}
