/* main */
.app-main {
  height: 100%;
  padding: 0 4rem;
}

.main-links {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: stretch;
  height: 100%;
  gap: 1rem;

  > div {
    /* aspect-ratio: 1 / 1.539; */
    background-color: #e6e6e6;
    border-radius: $radius-default;
    transform: scale(1);
    will-change: transform;
    transition: transform 0.4s ease-in-out;

    &:hover {
      transform: scale(0.95);

      > .main-copy {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        color: $font-white;
        font-weight: 700;
        font-size: 4rem;
        text-transform: uppercase;
        line-height: 4rem;
        text-align: center;
        text-shadow: 2px 2px 12px rgba(0, 0, 0, 0.35);
        background: rgba(255, 255, 255, 0.2);
        transform: scale(1);
        opacity: 1;
      }
    }

    > .main-copy {
      cursor: pointer;
      will-change: opacity transform;
      opacity: 0;
      transform: scale(0.6);
      transition: transform 0.4s ease-in-out;
    }

    &.main-link-ai {
      background-image: url("../../assets/images/main_visual_ai.webp");
      background-repeat: no-repeat;
      background-size: cover;
    }

    &.main-link-fb {
      background-image: url("../../assets/images/main_visual_fb.webp");
      background-repeat: no-repeat;
      background-size: cover;
    }

    &.main-link-info {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 2.4rem;
      letter-spacing: 0;
      text-align: center;
      line-height: 2.6rem;
    }
  }
}
