/* about */
.app-about {
  padding: 23.5rem 0 20rem 0;
  background-image: url("../../assets/images/bg_about_line.png");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: 100% auto;
}

.app-about .header-copy .imgbox {
  display: block;
}

.app-about .header-copy .copy {
  display: block;
  font-size: 4.6rem;
  line-height: 4rem;
  letter-spacing: -0.1rem;
  font-weight: 700;

  &.first-copy {
    padding: 9rem 0 2rem 11.6rem;
  }
  &.second-copy {
    padding: 0 0 28.4rem 11.6rem;
  }
}

.app-about .about-info {
  font-size: 2.4rem;
  letter-spacing: -0.2rem;
  font-weight: 700;
  padding: 0 0 0 11.6rem;
}

.app-about .about-info > span {
  display: block;
  padding: 0.8rem 0 0 0;
  font-weight: 500;
  font-size: 1.6rem;
  color: #878787;
  letter-spacing: 0;
}

.app-about .about-desc {
  width: 60%;
  font-size: 1.8rem;
  letter-spacing: -0.1rem;
  line-height: 2.4rem;
  padding: 4.5rem 0 0 11.6rem;
}

.app-about .about-focus {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  width: 100%;
  padding: 23.6rem 0 0;
  text-align: center;
}

.app-about .about-focus > span {
  display: block;
  font-weight: 700;
}

.app-about .about-focus > span:nth-child(2n + 1) {
  font-size: 12.6rem;
}

.app-about .about-focus > span:nth-child(2n) {
  font-size: 3.6rem;
}

.app-about .about-circle {
  width: 100%;
  height: 60rem;
  margin-bottom: 25.2rem;
  padding: 26.2rem 0 0 50rem;
}

.app-about .swiper-item {
  max-width: 80rem;
}

.app-about .swiper-item > h5 {
  margin: 0;
  font-size: 3.6rem;
  font-weight: 500;
  letter-spacing: -0.1rem;
  line-height: 7rem;
}

.app-about .swiper-item > p {
  font-size: 3rem;
  line-height: 5rem;
  letter-spacing: -0.1rem;
  font-weight: 500;
}
