body {
  font-size: 1.6rem;
  line-height: 2.2rem;
  font-weight: 500;
  color: $font-basic;
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI",
    "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  background-color: $bg-color;
  margin: 0;

  &.is-bg-darkgray {
    background-color: #757575;
  }

  &.is-bg-gray {
    background-color: #f0f0f0;
  }
}

a {
  color: $font-basic;
  text-decoration: none;
}

.app-wrap {
  min-width: 1023px;
  min-height: 667px;
  height: 100vh;
  padding: 0;
}

.app-container {
  &:has(.app-main) {
    height: calc(100% - (130px + 64px));
  }
}

.app-header {
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding: 5rem 4rem 3.2rem 4rem;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(16px);
  z-index: 1000;

  &.is-bg-darkbray {
    background: rgb(117, 117, 117, 0.5);
  }

  &.is-bg-gray {
    background: rgba(240, 240, 240, 0.5);
  }
}

.app-footer {
  padding: 2rem 4rem;

  address {
    font-size: 1.4rem;
    font-style: normal;
  }
}

.app-nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 4.4rem;

  a {
    padding: 0.5rem;
    font-weight: 700;
    font-size: 2rem;

    &.active,
    &:hover {
      text-decoration: underline;
      text-underline-offset: 8px;
    }
  }
}
.app-nopage {
  max-width: 100vw;
  max-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .no-page-wrap {
    font-size: 3.2rem;
    font-weight: 700;
    color: $font-basic;
    text-align: center;
  }
}
